#checkout-container {
  display: flex;
  font-size: 1.5rem;
  gap: 1rem;
}

#checkout-content {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
  gap: 1.5rem;
  text-align: center;
  margin-top: 10rem;
}

#checkout-details {
  font-weight: bold;
  border-bottom: 1px solid black;
}

#checkout-btn {
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: 2px solid #e74c3c;
  border-radius: 0.6em;
  color: #e74c3c;
  cursor: pointer;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-self: center;
  -ms-flex-item-align: center;
  align-self: center;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  padding: 1rem 1rem;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}
#checkout-btn:hover,
#checkout-btn:focus {
  color: #fff;
  outline: 0;
}

#checkout-btn {
  border-color: #3498db;
  color: #fff;
  box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
  -webkit-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}
#checkout-btn:hover {
  box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}
